:root {
  --header-height: 6rem;
  --gap-xs: .5rem;
  --gap-sm: 1.5rem;
  --gap-md: 2rem;
  --gap-lg: 3rem
  --gap-xl: 5rem;
  --margin-xs: .5rem
  --margin-sm: 1rem;
  --margin-md: 2rem;
  --margin-lg: 3rem ;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--ff-primary);
  font-size: var(--fs-300);
  color: var(--color-400);
  background-color: var(--color-100);
  min-height: 100vh;
  line-height: 1.5;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

h1, h2 {
  line-height: 1;
}

p {
  line-height: 1.4;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list],
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

button {
  background-color: transparent;
  cursor: pointer;
  border: none;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  /* font-family */
  --ff-primary: "Chivo", sans-serif;
  --ff-secondary: "Comfortaa", cursive;
}

:root {
  /** Colors **/
  --color-600: hsl(0, 0%, 0%);
  --color-500: hsl(30, 9%, 9%);
  --color-400: hsl(30, 7%, 11%);
  --color-300: hsl(0, 0%, 53%);
  --color-200: hsl(0, 0%, 87%);
  --color-100: hsl(0, 0%, 100%);
  --color-accent: hsl(22, 63%, 19%);
}

:root {
  /* font-sizes */
  --fs-biggest: 7rem;
  --fs-big: 4.375rem;
  --fs-900: 2.25rem;
  --fs-800: 2rem;
  --fs-700: 1.75rem;
  --fs-600: 1.5rem;
  --fs-500: 1.25rem;
  --fs-400: 1.125rem;
  --fs-300: 1rem;
  --fs-200: 0.875rem;
  /* font-weight */
  --fw-300: 300;
  --fw-400: 400;
  --fw-600: 600;
  --fw-700: 700;
}

.section-title {
  font-size: var(--fs-600);
  color: var(--color-600);
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.section-title--white {
  color: var(--color-100);
}
@media (min-width: 48rem) {
  .section-title {
    font-size: var(--fs-800);
  }
}

.section-heading {
  font-size: var(--fs-600);
  margin-bottom: 1.5rem;
  color: var(--color-500);
}
.section-heading--white {
  color: var(--color-100);
}
@media (min-width: 48rem) {
  .section-heading {
    font-size: var(--fs-800);
    margin-bottom: 2rem;
  }
}
.section-heading-2 {
  font-size: var(--fs-600);
}

.section-description {
  font-size: var(--fs-300);
  color: var(--color-300);
  line-height: 1.7;
}
@media (min-width: 48rem) {
  .section-description {
    font-size: var(--fs-300);
  }
}

.btn {
  display: inline-block;
  text-transform: uppercase;
  border: 1px solid var(--color-500);
  padding: 0.75rem 2rem;
  border-radius: 4px;
  font-size: var(--fs-300);
  font-weight: var(--fw-700);
  transition: all 0.3s;
}
@media (min-width: 48rem) {
  .btn {
    padding: 1rem 2.25rem;
    font-size: var(--fs-500);
  }
}
.btn--checkout {
  font-size: var(--fs-200);
  display: block;
  padding: 0.5rem 1rem;
}
.btn:hover {
  color: var(--color-100);
  background-color: var(--color-500);
}
.btn--white {
  border: 1px solid var(--color-100);
  color: var(--color-100);
  background-color: var(--color-600);
}
.btn--white:hover {
  background-color: var(--color-500);
}

.scroll-top {
  position: fixed;
  background-color: var(--color-accent);
  right: 1rem;
  bottom: -20%;
  padding: 0.4rem;
  display: flex;
  z-index: 10;
  transition: bottom 0.6s ease;
  border-radius: 3px;
}
.scroll-top.show-scroll {
  bottom: 2rem;
}

.card__container {
  position: relative;
}
.card__img {
  aspect-ratio: 1/1;
  object-fit: cover;
}
.card__description {
  padding: 0.5rem 0;
  display: grid;
}
.card__title {
  font-size: var(--fs-500);
  font-weight: var(--fw-300);
  line-height: 1.2;
  align-self: start;
}
.card__country {
  margin-bottom: 0.5rem;
}
.card__price {
  font-weight: var(--fw-700);
  font-size: var(--fs-400);
  align-self: end;
}
.card__btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  cursor: pointer;
  transition: stroke 0.4s;
}
.card__btn svg {
  pointer-events: none;
}

.cart-item {
  display: grid;
  grid-template-columns: auto 2.5fr 0.5fr 1fr;
  place-content: center;
  gap: 0.5rem;
  font-size: var(--fs-200);
}
.cart-item svg {
  pointer-events: none;
}
.cart-item p {
  justify-self: center;
}
.cart-item__info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.cart-item__remove {
  display: flex;
  align-items: center;
}
.cart-item__img {
  width: 40px;
  aspect-ratio: 1/1;
}
.cart-item__title {
  margin-left: 0.5rem;
  font-size: var(--fs-200);
}
.cart-item__price {
  justify-self: center;
  margin-right: 0.2rem;
}
.cart-item__units {
  display: flex;
  align-items: center;
  justify-self: center;
  gap: 0.5rem;
}

.cart-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cart-icon__quantity {
  position: absolute;
  top: -0.4rem;
  right: -0.5rem;
  color: black;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  font-size: 10px;
  display: grid;
  place-content: center;
}

.header {
  position: fixed;
  background-color: var(--color-100);
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}

.top-bar {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.top-bar__logo {
  width: 150px;
}
@media (min-width: 48rem) {
  .top-bar__logo {
    width: 200px;
  }
}

.main-nav {
  background-color: var(--color-500);
  color: var(--color-100);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  padding: 1.5rem;
  transform: translateY(-50%);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s, transform 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  gap: var(--gap-sm);
}
.main-nav.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.menu-btn {
  position: relative;
  width: 1.8rem;
  height: 1.125rem;
  background: transparent;
  z-index: 20;
  transition: transform 0.4s;
}
@media (min-width: 48rem) {
  .menu-btn {
    width: 2.1rem;
    height: 1.425rem;
  }
}
@media (min-width: 68.75rem) {
  .menu-btn {
    display: none;
  }
}
.menu-btn span {
  position: absolute;
  display: block;
  right: 0.1rem;
  width: 1.8rem;
  height: 3px;
  background: #000;
  transition: top 0.4s 0.4s, opacity 0.3s 0s, transform 0.4s 0s, background-color 0.4s;
  pointer-events: none;
}
@media (min-width: 48rem) {
  .menu-btn span {
    width: 2.5rem;
  }
}
@media (min-width: 68.75rem) {
  .menu-btn span {
    display: none;
  }
}
.menu-btn span:nth-child(1) {
  top: 0;
}
.menu-btn span:nth-child(2) {
  top: calc(50% - 1px);
}
.menu-btn span:nth-child(3) {
  top: calc(100% - 2px);
}
.menu-btn.active span {
  transition: top 0.4s 0s, opacity 0.3s 0s, transform 0.4s 0.4s, background-color 0.4s;
  background-color: #fff;
}
.menu-btn.active span:nth-child(1) {
  top: calc(50% - 1px);
  transform: rotate(45deg);
}
.menu-btn.active span:nth-child(2) {
  opacity: 0;
}
.menu-btn.active span:nth-child(3) {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
}

.socials__list {
